<template>
    <div class="user">
        <template>
            <el-table
            :data="userList"
            style="width: 100%">
                <el-table-column
                    prop="openid"
                    label="微信编号">
                </el-table-column>
                <el-table-column
                    prop="id"
                    label="用户编号">
                </el-table-column>
                <el-table-column
                    prop="times"
                    label="注册日期">
                </el-table-column>
                <el-table-column label="操作" v-if="roles == 'admin'">
                    <template slot-scope="scope">
                        <el-button
                        size="mini"
                        @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                        <el-button
                        size="mini"
                        type="danger"
                        @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </template>
    </div>
</template>

<script>
    export default {
        data () {
            return {
                userList:[],
                roles:""
            }
        },
        created() {
            this.getUser(); // 获取用户
            this.roles = this.$store.state.userInfo.roles;
        },
        methods: {
            
            getUser () {
                let that = this;
                let url = '/api'+this.$common.user_all;
                this.$http.post(url).then((res)=>{
                    if ( res.code == '200' ) {
                        that.userList = res.data;
                    } else if( res.code == '404' ){
                        sessionStorage.clear();
                        this.$router.push({ path:'/login' });
                    }              
                }).catch(data=>{

                })
            },
            handleEdit () {

            },
            handleDelete () {
                
            }
        },
        mounted() {
            
        },
    }
</script>

<style lang="scss" scoped>

</style>



